___CSS_LOADER_EXPORT___.push([module.id, `.sidebar-container {
  height: 100vh;
  position: fixed;
  top: 150px;
  z-index: 100000000;
  left: 1.5em;
  max-width: 400px;
  flex-grow: 40%;
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
}

.sidebar-item {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  text-decoration: none;
  color: #333;
  font-weight: 500;
  transition: background-color 0.2s;
  max-height: 1em;
  margin-bottom: 0.7em;
  border-radius: 30px;
  border: solid 0.1px #388e3c;
}

.sidebar-item:hover {
  background-color: #f1f1f1;
}

.sidebar-item.active {
  /* background-color: #388e3c; */
  /* color: #fafafa; */
  font-weight: bold;
  border: solid 2.7px #388e3c;
}

.sidebar-icon {
  margin-right: 15px;
  font-size: 20px;
}

.sidebar-text {
  font-size: 16px;
}

/* Optional: Styling for responsiveness */
@media (max-width: 768px) {
  .sidebar-container {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar-nav {
    flex-direction: row;
    overflow-x: auto;
    white-space: nowrap;
  }
  .sidebar-item {
    padding: 10px 15px;
    flex: 1;
    justify-content: center;
  }
  .sidebar-text {
    display: none;
  }
  .sidebar-icon {
    font-size: 24px;
  }
}
